<template>
  <div class="box">
    <!-- 顶部 -->
    <van-sticky>
      <div>
        <router-link to="/home">〈</router-link>
        <h2>我的</h2>
      </div>
    </van-sticky>
    <!-- 人头部分 -->
    <div class="user">
      <img src="//elm.cangdu.org/img/default.jpg" alt="" />
      <div>
        <p></p>
        <span><van-icon size="20" name="graphic" />暂无绑定手机号</span>
      </div>
      <p></p>
    </div>
    <!-- 余额等数据部分 -->
    <ul class="message">
      <li>
        <p><span style="color:#f90;">0.00</span>元</p>
        <span>我的余额</span>
      </li>
      <li>
        <p>
          <span style="color:#ff5f3e;">{{ user.gift_amount }}</span>
        </p>
        <span>我的优惠</span>
      </li>
      <li>
        <p><span style="color:#6ac20b;">0</span>分</p>
        <span>我的积分</span>
      </li>
    </ul>
    <img src="5bc7551845977d4fb18e9d91587af3d1.png" alt="" />
    <!-- 底部 -->
    <ul class="footer">
      <li
        @click="dian(item.id)"
        :key="item.id"
        v-for="item in foot"
        :class="item.flag ? 'active' : ''"
      >
        <van-icon size="26" :name="item.type" />
        <span>{{ item.text }}</span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      foot: [
        { text: "外卖", type: "phone-o", id: 0, flag: false },
        { text: "搜索", type: "eye-o", id: 1, flag: false },
        { text: "订单", type: "orders-o", id: 2, flag: false },
        { text: "我的", type: "manager", id: 3, flag: true },
      ],
      user:{},
      id:sessionStorage.getItem('token'),
    };
  },
  created() {},
  mounted() {
    this.getUser();
  },
  methods: {
    //点击底部
    dian(i) {
      this.foot[i].flag = true;
      this.foot.forEach((item) => {
        if (item.id != i) {
          item.flag = false;
        }
      });
      if (i == 0) {
        this.$router.push("/goods");
      }
    },
    async getUser() {
      let { data } = await this.$http.get(
        `http://elm.cangdu.org/v1/user?user_id=${this.id}`
      );
      this.user = data;
      console.log(this.user);
    },
  },
};
</script>
<style lang="scss" scoped>
// 不动的顶部
.van-sticky > div {
  width: 100%;
  height: 44px;
  background: #3190e8;
  color: white;
  display: flex;
  align-items: center;
  font-size: 16px;
  box-sizing: border-box;
  padding: 10px;
  h2 {
    margin-left: 36%;
  }
  a {
    color: white;
  }
}
// 人头部分
.user {
  width: 100%;
  height: 100px;
  background: #3190e8;
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
  padding: 20px;
  div {
    height: 40px;
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-content: space-between;
    width: 70%;
  }
  p {
    margin: 0;
    font-weight: bold;
    font-size: 18px;
    text-align: left;
  }
  span {
    font-size: 12px;
    display: flex;
    align-items: center;
  }
  img {
    width: 60px;
    height: 60px;
    border-radius: 50px;
  }
}
// 数据部分
.message {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: white;
  height: 90px;
  li {
    width: 33%;
    height: 100%;
    border-left: 1px solid #eee;
    > span {
      color: #666;
    }
    > p {
      margin: 10px;
      > span {
        font-size: 30px;
        font-weight: bold;
      }
    }
  }
}
// 大图部分
.box > img {
  width: 100%;
  margin-top: 10px;
}
// footer部分
.footer {
  position: fixed;
  width: 100%;
  height: 50px;
  border-top: 1px solid #ccc;
  background: white;
  display: flex;
  align-items: center;
  bottom: 0;
  li {
    width: 25%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    span {
      font-size: 12px;
    }
  }
}
.active {
  .van-icon {
    color: #3190e8;
  }
}
</style>
