import Vue from "vue";
import VueRouter from "vue-router";
import home from "../views/wrap/home.vue";
import login from "../views/wrap/login.vue";
import city from "../views/wrap/City.vue";
import user from "../views/wrap/user.vue";
import goods from "../views/wrap/goods.vue";
import Home from "../views/wrap/home.vue";
Vue.use(VueRouter);

const routes = [
 
  {
    path: "/about",
    name: "About",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: function() {
      return import(/* webpackChunkName: "about" */ "../views/About.vue");
    },
  },
  {
    path: "/",
    name: "home",
    component: home,
  },
  {
    path: "/login",
    name: "login",
    component: login,
  },
  {
    path: "/city",
    name: "city",
    component: city,
  },
  {
    path: "/user",
    name: "user",
    component: user,
  },
  {
    path: "/goods",
    name: "goods",
    component: goods,
  },
  {
    path: '/:id',
    name: 'Home',
    component: Home
  },
 
];

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes,
});

export default router;
