<template>
  <div>
    <app-header style="background:rgba(14, 173, 247, 0.979);">
      <div slot="left">
        <i class="el-icon-arrow-left" @click="$router.go(-1)"></i>
      </div>
      <div slot="title" style="font-size:20px">
        <p>{{ city.name }}</p>
      </div>
      <div slot="right" @click="$router.push('/home')">切换城市</div>
    </app-header>
    <!-- 输入搜索部分 -->
    <div class="center">
      <input
        type="text"
        placeholder="输入校园、商务楼、地址"
        v-model="text"
        @keyup.enter="all()"
      />
      <button @click="refer">提交</button>
      <span v-show="show" @click="text = ''">❌</span>
    </div>
    <p v-show="!flag">搜索历史</p>
    <ul v-show="flag">
      <li @click="dian(item)" v-for="(item, index) in searchCity" :key="index">
        <p>{{ item.name }}</p>
        <span>{{ item.address }}</span>
      </li>
    </ul>
     <ul v-show="!flag && arr.length!=0">
      <li @click="dian(item)" v-for="(item, index) in arr" :key="index">
        <p>{{ item.name }}</p>
        <span>{{ item.address }}</span>
      </li>
      <li class="del" @click="del()">
        <p>清空所有</p>
      </li>
    </ul>
  </div>
</template>

<script>
import appHeader from "../wrap/header";
export default {
  data() {
    return {
      city: JSON.parse(sessionStorage.getItem("city")), //当前城市
      text: "",
      searchCity: [],
      show: false,
      flag: false,
      arr:JSON.parse(localStorage.getItem('arr'))||[],
    };
  },
  created() {},
  mounted() {},
  watch: {
    arr:{
      handler(val) {
        localStorage.setItem("arr",JSON.stringify(val))
      },
      deep:true
    }
  },
  methods: {
    //提交事件
    refer() {
      this.flag = true;
      this.$http
        .get(
          `http://elm.cangdu.org/v1/pois?type=search&city_id=${this.city.id}&keyword=${this.text}`
        )
        .then((res) => {
          this.searchCity = res.data;
          console.log(this.searchCity);
        });
    },
    all() {
      this.flag = true;
      this.$http
        .get(
          `http://elm.cangdu.org/v1/pois?type=search&city_id=${this.city.id}&keyword=${this.text}`
        )
        .then((res) => {
          this.searchCity = res.data;
          console.log(this.searchCity);
        });
    },
    //每一个列表的点击事件
    dian(i) {
      this.arr.unshift(i);
      localStorage.setItem("arr", JSON.stringify(this.arr.slice(0,5)));
      this.$router.push("/goods");
    },
    del() {
      this.arr = []
    }
  },
  components: {
    appHeader,
  },
};
</script>

<style lang="scss" scoped>
// 中间的搜索部分
.center {
  width: 100%;
  height: 120px;
  background: white;
  margin-top: 16px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  padding: 12px 0;
  position: relative;
  input {
    width: 90%;
    height: 40px;
    border-radius: 5px;
    border: 1px solid #ccc;
    text-indent: 20px;
  }
  button {
    width: 92%;
    height: 40px;
    border-radius: 5px;
    background: #3190e8;
    color: white;
    border: none;
  }
}
//搜索列表
ul{
  margin-top: 2px;
  display: flex;
  flex-direction: column;
  li{
    width: 100%;
    height: 80px;
    background: white;
    margin-bottom: 1px;
    display: flex;
  flex-direction: column;
  text-align: left;
  box-sizing:border-box;
  padding: 0 20px;
    // p{

    // }
    span{
      color:#ccc;
      font-size: 14px;
    }
  }
}
.del{
  height: 50px;
  text-align: center;
}
</style>
