<template>
  <div>
    <app-header>
      <div slot="left">
        <i class="el-icon-arrow-left" @click="$router.go(-1)"></i>
      </div>
      <div slot="title">密码登录</div>
      <div slot="right"></div>
    </app-header>
    <div class="con">
      <el-input
        v-model="arr.username"
        placeholder="请输入号码"
        @blur="choose"
      ></el-input>
      <el-input v-model="arr.password" placeholder="请输入密码"></el-input>
      <div class="code">
        <el-input
          v-model="arr.captcha_code"
          placeholder="请输入验证码"
        ></el-input>
        <img :src="cap" alt="" />
        <div>
          <span>看不清</span
          ><span style="color: #3190e8" @click="ajaxCodeFn">换一张</span>
        </div>
      </div>
      <p>温馨提示：未注册过的账号，登录时将自动注册</p>
      <p>注册过的用户可凭账号密码登录</p>
      <van-button type="primary" size="large" @click="submit" :disabled="!can"
        >登录</van-button
      >
    </div>
  </div>
</template>

<script>
import appHeader from "../wrap/header";
import axios from "axios";
axios.defaults.withCredentials = true;
export default {
  data() {
    return {
      cap: "",
      arr: { username: "", password: "", captcha_code: "" },
      can: false,
    };
  },
  watch: {
    arr: {
      handler(val) {
        if (
          val.username != "" &&
          val.password != "" &&
          val.captcha_code != ""
        ) {
          this.can = true;
        } else {
          this.can = false;
        }
      },
      deep: true,
    },
  },
  created() {},
  mounted() {
    this.ajaxCodeFn();
  },
  methods: {
    setCookie(key, value, t) {
      var oDate = new Date();
      oDate.setDate(oDate.getDate() + t);
      document.cookie = key + "=" + value + "; expires=" + oDate.toDateString();
    },
    async ajaxCodeFn() {
      let { data: res } = await axios.post("http://elm.cangdu.org/v1/captchas");
      console.log(res);
      this.cap = res.code;
    },
    //登录
    async submit() {
      this.setCookie("cap", this.captcha_code, 2);
      this.setCookie("SID", 100, 2);
      // let { data: res } = ajaxLogin({
      //   username: this.username,
      //   password: this.password,
      //   captcha_code: this.captcha_code,
      // });

      let { data: res } = await axios.post("http://elm.cangdu.org/v2/login", {
        username: this.arr.username,
        password: this.arr.password,
        captcha_code: this.arr.captcha_code,
      });
      // 验证并跳转页面，把id设为token值，使PB页面发生变化
      if (res.message != "登录失败") {
        sessionStorage.setItem("token", JSON.stringify(res.user_id));
        console.log(res);
        this.$router.push("/home");
      }
    },
    choose() {
      if (!/^1[3456789]\d{9}$/.test(this.arr.username)) {
        alert("手机号输入错误，请重新输入");
        this.arr.username = "";
        return false;
      }
    },
  },
  components: {
    appHeader,
  },
};
</script>

<style lang="scss" scoped>
.el-input {
  padding: 10px;
  margin: 40px 0;
  height: 40px;
  box-sizing: border-box;
}
.code {
  display: flex;
  justify-content: space-between;
  align-items: center;
  img {
    width: 50px;
    height: 50px;
  }
}
</style>
