<template>
  <div class="home" :style="{ 'margin-top': flag ? '50px' : '0' }">
    <app-header :class="flag ? 'xi' : ''">
      <div slot="left">
        ele.me
      </div>
      <div v-show="!flag" slot="title"></div>
      <div v-show="flag" @click="toJump('goTop')" slot="title">置顶</div>
      <div slot="right" @click="jump('/login')">登录|注册</div>
    </app-header>
    <div class="top1">
      <span>当前定位城市：</span>
      <span style="font-weight:900">定位不准时，请在城市列表中选择</span>
    </div>
    <!-- 右侧锚点 -->
    <div class="cityList">
      <div v-for="(item, index) in list" :key="index">
        <span @click="toJump(item)">{{ item }}</span>
      </div>
    </div>
    <!-- 当前城市 -->
    <div class="top">
      <span>{{ guess.name }}</span>
      <i class="el-icon-arrow-right"></i>
    </div>
    <!-- 热门城市 -->
    <div>
      <p style="margin:8px 0;">热门城市</p>
      <ul class="hot">
        <li v-for="(item, index) in hot" :key="index" @click="tiao(item.id)">
          {{ item.name }}
        </li>
      </ul>
    </div>
    <!-- A-Z城市 -->
    <!-- 写法111111111111111111111 -->
    <!-- <div>
      <div v-for="(value, key) in group" :key="key">
        <p>{{ key }}</p>
        <ul class="hot">
          <li v-for="(t, i) in value" :key="i">{{ t.name }}</li>
        </ul>
      </div>
    </div> -->

    <!-- 写法2222222222222222222 -->
    <div>
      <div v-for="(item, index) in list" :key="index">
        <p :id="item" :ref="item">{{ item }}</p>
        <ul class="hot">
          <li v-for="(t, i) in group[item]" :key="i">
            <span @click="tiao(t.id)">{{ t.name }}</span>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import appHeader from "../wrap/header";
import { ajaxCity } from "../../utils/api";
export default {
  data() {
    return {
      guess: {}, //当前城市存储数据的
      hot: [], //热门城市
      group: {}, //分组城市
      list: [], //字母集合
      flag: false,
    };
  },
  created() {},
  //挂载后
  mounted() {
    this.ajaxGuess();
    // 滚动条的获取
    window.addEventListener("scroll", this.handleScrollx, true);
  },
  components: {
    appHeader,
  },
  methods: {
    async ajaxGuess() {
      //请求当前城市
      let { data } = await ajaxCity("guess");
      //   console.log(data);
      this.guess = data;
      // 请求热门城市
      let { data: hot } = await ajaxCity("hot");
      //   console.log(res);
      this.hot = hot;
      // 请求分组城市
      let { data: resgroup } = await ajaxCity("group");
      console.log(resgroup);
      //写法111111111111111111111
      //   Object.keys(resgroup).sort().map((item) => {
      //       this.$set(this.group, item, resgroup[item]);
      //     });
      //   console.log(this.group);

      // 写法222222222222222
      this.group = resgroup;
      this.list = Object.keys(resgroup).sort();
      console.log(resgroup, hot);

    },
    //跳转到指定位置
    toJump(z) {
      if (z != "goTop") {
        document.body.scrollTop = document.documentElement.scrollTop =
          this.$refs[z][0].offsetTop - 50;
      } else {
        document.body.scrollTop = document.documentElement.scrollTop = 0;
      }
      // console.log(this.$refs[z][0].offsetTop - 50)
    },
    handleScrollx() {
      console.log("滚动高度", window.pageYOffset);
      if (window.pageYOffset > 50) {
        this.flag = true;
      } else {
        this.flag = false;
      }
      console.log(this.flag);
    },
    //跳转方法
    jump(path, data) {
      this.$router.push({
        path,
        query: {
          data,
        },
      });
    },
    //跳转到详情页面
    async tiao(i) {
      console.log(i)
      let {data} =await this.$http.get(`http://elm.cangdu.org/v1/cities/${i}`);
     sessionStorage.setItem("city", JSON.stringify(data));
     this.$router.push({
        path: "/city"
     })
      
    },
  },
};
</script>

<style scoped lang="scss">
.top {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
  box-sizing: border-box;
  height: 30px;
  align-items: center;
  background-color: #f0f0f0;
  span {
    color: blue;
  }
}
.top1 {
  height: 40px;
  border-bottom: 1px solid #ccc;
  font-size: 12px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
  padding: 10px;
  background: white;
}
.top1 span {
  color: #9f9f9f;
}
.hot {
  display: flex;
  flex-wrap: wrap;

  li {
    height: 40px;
    line-height: 40px;
    width: 25%;
    border: 1px solid #f0f0f0;
    box-sizing: border-box;
    text-align: center;
    color: rgba(9, 13, 243, 0.938);
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
.cityList {
  position: fixed;
  right: 0;
  top: 20%;
  z-index: 100;
  background-color: #fff;
}
.xi {
  position: fixed;
  top: 0;
  left: 0;
  transition: all 0.5s;
}
</style>
